<template>
  <!--============
  =======  videos__list
============================= -->

  <div class="videos__list--item d-flex flex-wrap ">
    <!-- I check if video.id.kind == youtube#video  then i display thumbnails of Video and redirect to video details-->
    <router-link
      v-if="video.id && video.id.kind == 'youtube#video'"
      :to="{ path: `/video/${video.id.videoId}` }"
    >
      <div class="videos__list--item-image">
        <img :src="video.snippet.thumbnails.medium.url" alt="" />
        <span>10:2</span>
      </div>
    </router-link>

    <!-- I check if video.id.kind == youtube#playlist  then i display thumbnails of channel and redirect to channel-->
    <router-link
      v-else-if=" (video.id && video.id.kind == 'youtube#channel') ||
            (video.id && video.kind == 'youtube#playlist')"
      :to="{
        path:
          video.kind == 'youtube#playlist'
            ? `/video/list/${video.id}`
            : `/channel/${video.id.channelId}`,
      }"
    >
      <div
        class="videos__list--item-image"
       
      >
        <img :src="video.snippet.thumbnails.medium.url" alt="" />
        <span>10:2</span>

        <div
          v-if="video && video.id && video.kind == 'youtube#playlist'"
          class="videos__list--playlist"
        >
          <div class="video__playlist--count">
            {{ video.contentDetails.itemCount }}
          </div>
          <div class="videos__playlist--icon">
            <i class="icon icon-video-list"></i>
          </div>
        </div>
      </div>
    </router-link>

    <router-link
      v-else-if="video.id && video.kind == 'youtube#playlistItem'"
      :to="{ path: `/video/list/${video.snippet.resourceId.videoId}` }"
    >
      <div class="videos__list--item-image">
        <img :src="video.snippet.thumbnails.medium.url" alt="" />
        <span>10:2</span>
      </div>
    </router-link>

    <div class="videos__list--item-detail">
      <!-- I check if video.id.kind == youtube#video  then i display title of Video and redirect to video details-->
      <router-link
        v-if="video.id && video.id.kind == 'youtube#video'"
        :to="{ path: `/video/${video.id.videoId}` }"
      >
        <h3
          v-if="video && video.snippet && video.snippet.title"
          class="videos__list--item-title"
        >
          {{ video.snippet.title }}
        </h3>
      </router-link>

      <!-- I check if video.id.kind == youtube#channel  then i display title of channel and redirect to channel-->

      <router-link
        v-else-if="
          (video.id && video.id.kind == 'youtube#channel') ||
            (video.id && video.kind == 'youtube#playlist')
        "
        :to="{ path: `/channel/${video.snippet.channelId}` }"
      >
        <h3 class="videos__list--item-title">
          {{ video.snippet.title }}
        </h3>
      </router-link>

      <router-link
        v-if="video && video.snippet"
        :to="{ path: `/channel/${video.snippet.channelId}` }"
      >
        <p class="videos__list--item-channel">
          {{ video.snippet.channelTitle }}
        </p>
        <span>
          1500 views
        </span>
      </router-link>
    </div>
  </div>
  <!-- end-videos__list--item -->
</template>

<script>
export default {
  name: "video-item",
  props: ["video"],
  mounted() {},
};
</script>
