<template>
 <section class="filter">
      <div class="container">
        <div class="container__row align-items-center">
            <div class="container__row align-items-center">
          <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3 mr-auto">
            <div class="filter__result">
            <p>About <span>{{totalResults}}</span> result</p> 
          </div>
          </div><!-- end-col-3 -->

          <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3">
            <div class="filter__button">
            <button class="btn d-flex align-items-center" type="button" @click="openFilter()">
              <img src="../../assets/img/icons/filter.svg" alt="">
              <span> Filter </span>
             </button>
            </div>
          </div><!-- end-col-3 -->
        </div> <!-- end- container__row -->

        <div class="container__row">
          <div class="filter__types w-100" :class="!active ? 'active':''">
            <filterItem />
          </div>
        </div>
        </div>
      </div>
 </section>
</template>

<script>
import filterItem from '../../components/FilterItem/FilterItem';
import { bus } from '../../main';

export default {
  name: 'filter',
  props:['totalResults'],
  components:{
    filterItem
  },
  data(){
    return{
      active:false
    }
  },
  methods:{
    openFilter(){
      this.active = !this.active
    }
  
  },
  created(){
    bus.$on('closeFilter',()=>{
      this.openFilter()
    })
  }
}
</script>
