import Vue from 'vue';
import App from './App.vue';
import { store } from './store';

import VueRouter from 'vue-router';
import router from './routers';
import ScrollLoader from 'vue-scroll-loader'

import axios from 'axios'
import VueAxios from 'vue-axios'
import './assets/app.css';
Vue.use(ScrollLoader)

Vue.use(VueRouter);
Vue.use(VueAxios, axios)

Vue.config.productionTip = false;

export const bus = new Vue();

Vue.component('loading', require('./components/loading.vue').default);
Vue.component('headerItem', require('./components/HeaderItem/HeaderItem.vue').default);
Vue.component('videoItem', require('./components/VideoItem/VideoItem').default);
Vue.component('channelView', require('./views/Channel/Channel.vue').default);
Vue.component('videoDetails', require('./views/VideoDetails/VideoDetails.vue').default);
Vue.component('VideoPlaylistDetails', require('./views/VideoDetails/VideoPlaylistDetails.vue').default);

new Vue({
  router,
  store,

  render: h => h(App)
}).$mount('#app')
