<template>
  <header class="header__section fixed">
    <div class="container">
      <div class="container__row">
        <div class="top__header d-flex w-100 align-items-center">
        <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3">
          <div class="top__header--logo mr-auto">
            <router-link to="/" v-if="!isMobile">
            <img class="img-fluid" src="../../assets/img/logo.png" alt="" >
          </router-link>
          <router-link to="/" v-else>
           <i class="fab fa-youtube"></i>
          </router-link>
          </div>
        </div> <!-- end-col-3 -->

        <div class="col-xl-9 col-lg-9 col-md-9 col-sm-12 col-xs-12">
        <searchInput/>
        </div> <!-- end-col-9 -->
      </div>
      </div> <!-- end- container__row -->
    </div>
  </header><!-- end-header -->

</template>

<script>
import searchInput from './SearchItem/SearchInput';

export default {
    name:"header-section",
    props:[],
    data(){
      return{
        innerWidth:425,
        windowWidth: window.innerWidth
      }
    },
    components:{
        searchInput
    },
  mounted(){
    window.addEventListener('resize', () => {
      this.windowWidth = window.innerWidth
      console.log(this.isMobile)
    })
  },
computed: {
    isMobile() {
      return this.windowWidth <= 768
    }
  }
}
</script>