<template>
  <!--============
  =======  channel
============================= -->
  <section class="channel mt-3">
    <div class="container">
      <div class="container__row">
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div
            class="channel__banner"
            v-if="data && data.brandingSettings && data.brandingSettings.image"
            :style="{
              backgroundImage: `url('${data.brandingSettings.image.bannerExternalUrl}')`,
            }"
          ></div>
        </div>
        <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div class="channel__detail d-flex align-items-center">
            <div class="channel__detail--avatar">
              <img
                class="img-fluid"
                v-if="data && data.snippet && data.snippet.thumbnails"
                :src="data.snippet.thumbnails.medium.url"
                alt=""
              />
            </div>
            <div class="channel__detail-text">
              <div class="channel__detail--title">
                <h4 v-if="data && data.snippet && data.snippet.title">{{ data.snippet.title }}</h4>
              </div>
              <div class="channel__detail--subscribe d-flex">
                 <div class="channel__details--videos-count">
                  <span v-if="data && data.statistics && data.statistics.videoCount">{{ data.statistics.videoCount }}  Videos </span>
                
                </div>
                <div class="channel__details--videos-subscribe d-flex">
                <p><i class="fab fa-youtube"></i> subscribe</p>
                <span v-if="data && data.statistics && data.statistics.subscriberCount">{{ subscribersCount }}</span>
                </div>
               
              </div>
            </div>
          </div>
       
        </div>
      </div>
    </div>
  </section>
</template>

<script>

export default {
  name: "",
  props: ["data"],
  data() {
    return {
      
    };
  },
  computed: {
    subscribersCount() {
      let count = this.data.statistics.subscriberCount;
      if (count >= 1000000) {
        count = count / 1000000 + "M";
      } else if (count >= 1000) {
        count = count / 1000 + "K";
      }
      return count;
    },
  },
};
</script>
