<template>
  <!--============
  =======  video__details
============================= -->
  <div>
    <headerItem />
    <section class="video__details mt-3">
      <div class="container">
        <div class="container__row">
          <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <div v-if="videoDetails && videoDetails.player && videoDetails.player.embedHtml" v-html="videoDetails.player.embedHtml"></div>
          </div>
        </div>
        <div class="container__row">
          <div class="video__details--container w-100 border__bottom">
            <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
              <div class="video__details--title">
                <h2>{{ videoDetails.snippet.title }}</h2>
              </div>
            </div>
            <!-- end col-12 -->
            <div
              class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 d-flex align-items-center video__details--info"
            >
              <div class="col-xl-4 col-lg-4 col-md-4 col-sm-4 col-xs-4 mr-auto">
                <div class="video__details--views" v-if="videoDetails && videoDetails.player && videoDetails.player.viewCount">
                  <p>{{videoDetails.player.viewCount}} watching now</p>
                </div>
              </div>
              <!-- end col-4 -->

              <div
                class="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-xs-6 d-flex video__info--items align-items-center"
              >
                <div class="video__icons--container d-flex align-items-center">
                  <button
                    type="button"
                    class="btn video__info--icon"
                    v-if="
                      videoDetails &&
                        videoDetails.statistics &&
                        videoDetails.statistics.dislikeCount
                    "
                  >
                    <i class="icon icon-like"></i>
                    <span>{{ videoDetails.statistics.dislikeCount }}</span>
                  </button>
                  <button
                    type="button"
                    class="btn video__info--icon"
                    v-if="
                      videoDetails &&
                        videoDetails.statistics &&
                        videoDetails.statistics.likeCount
                    "
                  >
                    <i class="icon icon-dislike"></i>
                    <span>{{ videoDetails.statistics.likeCount }}</span>
                  </button>
                  <div class="video__info--icon-border"></div>
                </div>
                <button type="button" class="btn video__info--icon">
                  <i class="icon icon-share"></i> <span>SHARE</span>
                </button>
                <button type="button" class="btn video__info--icon">
                  <i class="icon icon-save-search"></i>
                </button>
                <button type="button" class="btn video__info--icon">
                  <i class="fas fa-ellipsis-h"></i>
                </button>
              </div>
              <!-- end col-6 -->
            </div>
            <!-- end col-12 -->
          </div>
          <div class="video__details--channel w-100 border__bottom">
            <div
              class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 d-flex align-items-center"
            >
              <div
                class="video__details--channel-avater"
                v-if="videoDetails.snippet.title"
              >
                {{ videoDetails.snippet.title.charAt(0) }}
              </div>

              <!-- video__details--channel-info -->
              <div class="video__details--channel-info">
                <router-link
                  :to="{ path: `/channel/${videoDetails.snippet.channelId}` }"
                >
                  <h3>{{ videoDetails.snippet.channelTitle }}</h3>
                </router-link>
                <p>published {{ videoDetails.snippet.publishedAt }}</p>
              </div>
              <!-- end video__details--channel-info -->
            </div>
          </div>
          <!-- video__details--channel -->
        </div>
        <!-- end container__row -->
      </div>
    </section>
      <section class="videos__list">
    <div class="container">
      <div class="container__row">
        <div class="videos__list--bg w-100">
    <videoItem
      v-for="video in relatedVidoesIDs.length > 0
        ? relatedVidoesIDs
        : playlistVideos"
      :key="video.etag"
      :video="video"
    />
  </div>
      </div>
    </div>
      </section>
    </div>
</template>

<script>
import {
  getVideoDetail,
  getRelatedVideo,
  getplaylistVideos,
} from "../../servicesRequests";
import { bus } from "../../main";
export default {
  name: "video-details",
  data() {
    return {
      id: this.$route.params.id,
      //type: this.$route.params.query,
      videoDetails: {},
      relatedVidoesIDs: [],
      playlistVideos: [],
      firstVideo:{}
    };
  },
  methods: {
    getVideoDetails() {
 
        getVideoDetail(this.id)
          .then((response) => {
            this.videoDetails = response.data.items[0];
            this.getRelatedVideos();
          })
          .catch(function(error) {
            // handle error
            console.log(error);
          });
      
    },
    getPlaylistsVideo() {
      getplaylistVideos(this.id).then((response) => {
        this.firstVideo = response.data.items[0];
        this.playlistVideos = response.data.items;
        this.id = this.firstVideo.snippet.resourceId.videoId;
        this.getVideoDetails()
        //
      });
    },

    getRelatedVideos() {
      getRelatedVideo(this.id)
        .then((response) => {
          this.relatedVidoesIDs = response.data.items;
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    },
    update() {
     
      
  }
  },
  created() {
  
    this.getVideoDetails();
    bus.$on("getQuery", (data) => {
      this.term = data;
    });
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.getVideoDetails();
    },
  },
};
</script>
