import Vue from 'vue';
import Router from 'vue-router';
import SearchResult from '@/views/SearchResult/SearchResult.vue';
import Channel from '@/views/Channel/Channel.vue';
import VideoDetails from '@/views/VideoDetails/VideoDetails.vue';
import VideoPlaylistDetails from '@/views/VideoDetails/VideoPlaylistDetails.vue';
import Notfound from '@/views/NotFound/404.vue';

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '',
            name: 'search-result',
            component: SearchResult
        },
        {
            path: `/search`,
            name: 'search-result',
            component: SearchResult
        },
        {
            path: '/channel/:id',
            name: 'channel',
            component: Channel,
        },
        {
            path: '/video/:id',
            name: 'video',
            component: VideoDetails
            
        },
        {
            path: '/video/list/:id',
            name: 'List',
            component: VideoPlaylistDetails
            
        },
        {
            path: '/404',
            name: '404',
            component: Notfound
        },
        {
            path: '*',
            redirect: '/404',

        }
    ]
});


export default router