import http from './httpServices';
import YoutubeAPI from './env.js';

const domain = 'https://youtube.googleapis.com/youtube/v3';

// this request take Term or any text to search by it and return result

export const getResultItems = term => http.get(`${domain}/search?part=snippet&key=${YoutubeAPI.API_KEY}&q=${term}`
);
// this request take Term or any text to search by filters it and return result
export const getResultItemsByFilter = (order,type) => http.get(`${domain}/search?part=snippet&order=${order}&type=${type}&key=${YoutubeAPI.API_KEY}`
);


// this request take nextPageToken to get newest Items from next Pages
export const gotoNextPages = nextPageToken => http.get(`${domain}/search?part=snippet&maxResults=10&order=viewCount&pageToken=${nextPageToken}&q=skateboarding%20dog&type=video&key=${YoutubeAPI.API_KEY}`
);

// this request responsible for display  selected video Detail

export const getVideoDetail = id => http.get(`${domain}/videos?part=snippet%2C%20player%2C%20statistics&id=${id}&maxHeight=565&maxWidth=1150&key=${YoutubeAPI.API_KEY}`
);

// this request responsible for display related videos in video details page

export const getRelatedVideo = id => http.get(`${domain}/search?part=snippet&relatedToVideoId=${id}&type=video&key=${YoutubeAPI.API_KEY}`
);


// this request responsible for display channel details

export const getChannelDetail = id => http.get(`${domain}/channels?part=brandingSettings%2Csnippet%2CcontentDetails%2Cstatistics&id=${id}&key=${YoutubeAPI.API_KEY}`
);

// this request responsible for display playlists

export const getChannelPlaylist = (id, maxResults) => http.get(`${domain}/playlists?part=snippet%2CcontentDetails&channelId=${id}&maxResults=${maxResults}&key=${YoutubeAPI.API_KEY}`
);

// this request responsible for display playlistVideos

export const getplaylistVideos = id => http.get(`${domain}/playlistItems?part=snippet%2CcontentDetails&playlistId=${id}&key=${YoutubeAPI.API_KEY}`
);

// this request responsible for display channelSection

export const getChannelSection = id => http.get(`${domain}/channelSections?part=snippet%2CcontentDetails&channelId=${id}&key=${YoutubeAPI.API_KEY}`
);