<template>
  <div class="top__header-form">
            <form action="" @submit.prevent="saveTermToStore()">
              <div class="top__header-form--input w-100 d-flex">
                <input class="form-control" type="text" v-model="query" placeholder="Search">
                <button class="btn top__header-form--btn" type="submit"><i class="fas fa-search"></i></button>
              </div>
            </form>
          </div>
</template>

<script>

export default {
    name:"search-input",
    data(){
      return{
        query:'',
       
      }
    },
    props:[],

    methods:{
      saveTermToStore:function (){
        this.$store.commit('SearchQuery',this.query);
        if(window.localStorage.searchTerm !=''){
          this.$router.replace({ path: '/search', query:{query:window.localStorage.searchTerm}})
          console.log('value')
        }else{

          this.$router.replace({ path: '/search', query:{query:this.query}})
           console.log('empty')
        }

        // bus.$emit('getQuery',this.query);
      }
    },
    computed:{
      searchLoactStorage(){
        return this.$store.state.searchTerm
      }
    },
    watch:{
      "$route"(to){
        this.query = to.query.query
      } 
     }
    
}
</script>