<template>
  <div>
    <div id="search-result">
      <headerItem/>
      <filterList :totalResults="this.$store.state.SearchResultData.pageInfo.totalResults" class="mt-3"/>
        <div class="videos__list">
    <div class="container">
      <div class="container__row">
        <div class="videos__list--bg w-100">
      <videoItem
        v-for="video in this.$store.state.SearchResultData.items"
        :key="video.videoId"
        :video="video"
      />
        </div>
      </div>
    </div>
        </div>
      
    </div>
    <scroll-loader :loader-method="getNextPage" :loader-enable="loading">
    </scroll-loader>
  </div>
</template>

<script>
import filterList from "../Filter/FilterView";


export default {
  name: "search-result",
  data() {
    return {
      term: "",
      loading:true
      
    };
  },
  components: {
    filterList,
    
  },
  methods: {
    getNextPage(){
      this.$store.commit('gotoNextPage')
    },

  },
  mounted() {
   this.getNextPage()
  },
  created() {
    if (this.term != null) {
      this.$store.commit('getSearchResult');
    }
      this.term = window.localStorage.searchTerm;

  },
  watch: {
    $route(to) {
      this.term = to.query.query;
      this.term = window.localStorage.searchTerm;
      this.$store.commit('getSearchResult');
    },
  },
};
</script>
