import Vue from 'vue'
import Vuex from 'vuex'
import {getResultItems,gotoNextPages,getResultItemsByFilter} from './servicesRequests';
Vue.use(Vuex);
let searchTerm = window.localStorage.getItem('searchValue');

export const store = new Vuex.Store({

    state: {
        SearchResultData: {
            items: [],
            pageInfo: {},
            nextPageToken: null,
          },
          filters:{
              sortby:'relevance',
              type:'video'
          },
        searchInput:searchTerm,
    },
    mutations: {
        SearchQuery:function (state,term){
            //let self = this;
            state.searchInput = term;
            window.localStorage.setItem('searchTerm', state.searchInput);
            this.commit('getSearchResult')
        },
        getSearchResult:function(state){
            state.SearchResultData.nextPageToken = null;
            getResultItems(state.searchInput)
              .then((response) => {
                state.SearchResultData = [];
                state.term = "";
                state.SearchResultData = response.data;
                //this.loading = false;
              })
              .catch(function(error) {
                // handle error
                console.log(error);
              });
        },
        getResultItemsByFilters(state,filters){
            console.log(filters)
            state.filters.sortby = filters.sortby;
            state.filters.type = filters.type;
           
            state.SearchResultData.nextPageToken = null;
            getResultItemsByFilter(state.filters.sortby,state.filters.type)
            
              .then((response) => {
                state.SearchResultData = [];
                state.term = "";
                state.SearchResultData = response.data;
                //this.loading = false;
              })
              .catch(function(error) {
                // handle error
                console.log(error);
              });
        },
        gotoNextPage(state) {
            if (!this.loading) {
              gotoNextPages(state.SearchResultData.nextPageToken).then((response) => {
                if (response.data.items.length > 0) {
                  state.SearchResultData.items.push(...response.data.items);
                  state.SearchResultData.pageInfo = response.data.pageInfo;
                  state.SearchResultData.nextPageToken = response.data.nextPageToken;
                }
              });
            }
          },

    },
  
});