<template>
  <div class="">
    <headerItem />
    <channelInfo :data="channelInfos"/>
  <div class="videos__list">
    <div class="container">
      <div class="container__row">
        <div class="videos__list--bg w-100">
    <videoItem
      v-for="video in getChannelPlaylist"
      :key="video.etag"
      :video="video"
    />
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
import { getChannelDetail, getChannelPlaylist, getChannelSection } from "../../servicesRequests";
import channelInfo from "../../components/ChannelInfo/ChannelInfo";

export default {
  name: "channel",
  components: {
    channelInfo,
  },
  data() {
    return {
      id: this.$route.params.id,
      type: this.$route.params.query,
      maxResults: 25,
      channelInfos: {},
      getChannelPlaylist: [],
      getChannelSection:{}
    };
  },
  methods: {
    getChannelDetails() {
      getChannelDetail(this.id)
        .then((response) => {
          this.channelInfos = response.data.items[0];
          this.getChannelPlaylists();
          this.getFeaturedChannel(this.channelInfos.id);
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    },
    getChannelPlaylists() {
      getChannelPlaylist(this.id , this.maxResults)
        .then((response) => {
          this.getChannelPlaylist = response.data.items;
          console.log(response.data);
        })
        .catch(function(error) {
          // handle error
          console.log(error);
        });
    },

       getFeaturedChannel(channelId){
        getChannelSection(channelId).then(response => {
          this.getChannelSection = response
    })
   }
  },
  created() {
    this.getChannelDetails();
  },
};
</script>
